.ama__subscribe-promo {
  display: inline-block;
  padding: $gutter / 2;
  color: $white;
  background: $purple;

  &__subject-title {
    @extend .ama__h4--white;
    padding-bottom: $gutter / 2;
  }

  &__form {
    .ama__button {
      display: block;
      margin: 0 auto;
      &:hover {
        background: $hoverPurple;
      }
    }
  }

  .ama__form-group {
    input {
      @include type($myriad-pro, $font-weight-regular);
      font-style: italic;
    }

    label {
      visibility: hidden;
    }
  }

  &--wide {
    width: 100%;

    .ama__subscribe-promo__container {
      display: flex;
      flex-direction: column;

      @include breakpoint($bp-small min-width) {
        flex-direction: row;
      }
    }

    .ama__subscribe-promo__content {
      flex: 1;
    }

    .ama__subscribe-promo__form {
      display: flex;
      flex-direction: column;

      .ama__form-group {
        margin: 0;
      }

      .ama__form-group label {
        display: none;
      }

      .ama__form-group input {
        @include gutter($margin-bottom-full...);
        height: 49px;
      }

      @include breakpoint($bp-small min-width) {
        .ama__button {
          margin: 0 0 0 auto;
        }
      }

      @include breakpoint($bp-med min-width) {
        flex-direction: row;
        height: 49px;

        .ama__form-group input {
          margin-bottom: 0;
        }
      }
    }
  }
}
